@import "tailwindcss";

@plugin "tailwindcss-animate";

@custom-variant dark (&:is(.dark *));

@theme inline {
  --color-background: var(--background);
  --color-foreground: var(--foreground);
  --color-card: var(--card);
  --color-card-foreground: var(--card-foreground);
  --color-popover: var(--popover);
  --color-popover-foreground: var(--popover-foreground);
  --color-primary: var(--primary);
  --color-primary-foreground: var(--primary-foreground);
  --color-secondary: var(--secondary);
  --color-secondary-foreground: var(--secondary-foreground);
  --color-muted: var(--muted);
  --color-muted-foreground: var(--muted-foreground);
  --color-accent: var(--accent);
  --color-accent-foreground: var(--accent-foreground);
  --color-destructive: var(--destructive);
  --color-destructive-foreground: var(--destructive-foreground);
  --color-border: var(--border);
  --color-input: var(--input);
  --color-ring: var(--ring);
  --color-chart-1: var(--chart-1);
  --color-chart-2: var(--chart-2);
  --color-chart-3: var(--chart-3);
  --color-chart-4: var(--chart-4);
  --color-chart-5: var(--chart-5);
  --radius-sm: calc(var(--radius) - 4px);
  --radius-md: calc(var(--radius) - 2px);
  --radius-lg: var(--radius);
  --radius-xl: calc(var(--radius) + 4px);
  --color-sidebar: var(--sidebar-background);
  --color-sidebar-foreground: var(--sidebar-foreground);
  --color-sidebar-primary: var(--sidebar-primary);
  --color-sidebar-primary-foreground: var(--sidebar-primary-foreground);
  --color-sidebar-accent: var(--sidebar-accent);
  --color-sidebar-accent-foreground: var(--sidebar-accent-foreground);
  --color-sidebar-border: var(--sidebar-border);
  --color-sidebar-ring: var(--sidebar-ring);
}

:root {
  --background: oklch(0.98 0.01 80);
  --foreground: oklch(0.24 0.02 240);

  --card: oklch(0.99 0.01 80);
  --card-foreground: oklch(0.24 0.02 240);

  --popover: oklch(0.99 0.01 80);
  --popover-foreground: oklch(0.24 0.02 240);

  --primary: oklch(0.53 0.13 190);
  --primary-foreground: oklch(0.98 0.01 80);

  --secondary: oklch(0.94 0.02 85);
  --secondary-foreground: oklch(0.33 0.04 210);

  --muted: oklch(0.93 0.02 85);
  --muted-foreground: oklch(0.49 0.03 220);

  --accent: oklch(0.83 0.11 70);
  --accent-foreground: oklch(0.32 0.06 40);

  --destructive: oklch(0.62 0.17 25);
  --destructive-foreground: oklch(0.98 0.01 80);

  --border: oklch(0.9 0.02 85);
  --input: oklch(0.96 0.02 85);
  --ring: oklch(0.53 0.13 190 / 0.25);

  --radius: 1rem;

  --sidebar: oklch(0.96 0.02 85);
  --sidebar-foreground: oklch(0.32 0.03 220);
  --sidebar-primary: oklch(0.53 0.13 190);
  --sidebar-primary-foreground: oklch(0.98 0.01 80);
  --sidebar-accent: oklch(0.86 0.07 70);
  --sidebar-accent-foreground: oklch(0.28 0.05 40);
  --sidebar-border: oklch(0.9 0.02 85);
  --sidebar-ring: oklch(0.53 0.13 190 / 0.18);

  --chart-1: oklch(0.53 0.13 190);
  --chart-2: oklch(0.62 0.11 155);
  --chart-3: oklch(0.71 0.12 75);
  --chart-4: oklch(0.67 0.14 35);
  --chart-5: oklch(0.55 0.1 215);
}

.dark {
  --background: oklch(0.16 0.02 200);
  --foreground: oklch(0.95 0.01 90);

  --card: oklch(0.2 0.02 200);
  --card-foreground: oklch(0.95 0.01 90);

  --popover: oklch(0.2 0.02 200);
  --popover-foreground: oklch(0.95 0.01 90);

  --primary: oklch(0.7 0.12 190);
  --primary-foreground: oklch(0.16 0.02 200);

  --secondary: oklch(0.26 0.03 190);
  --secondary-foreground: oklch(0.92 0.02 90);

  --muted: oklch(0.26 0.03 190);
  --muted-foreground: oklch(0.75 0.03 90);

  --accent: oklch(0.7 0.11 70);
  --accent-foreground: oklch(0.18 0.02 200);

  --destructive: oklch(0.66 0.18 25);
  --destructive-foreground: oklch(0.95 0.01 90);

  --border: oklch(0.28 0.03 200);
  --input: oklch(0.26 0.03 200);
  --ring: oklch(0.7 0.12 190 / 0.35);

  --sidebar: oklch(0.15 0.02 200);
  --sidebar-foreground: oklch(0.9 0.02 90);
  --sidebar-primary: oklch(0.7 0.12 190);
  --sidebar-primary-foreground: oklch(0.16 0.02 200);
  --sidebar-accent: oklch(0.65 0.1 70);
  --sidebar-accent-foreground: oklch(0.16 0.02 200);
  --sidebar-border: oklch(0.26 0.03 200);
  --sidebar-ring: oklch(0.7 0.12 190 / 0.3);

  --chart-1: oklch(0.7 0.12 190);
  --chart-2: oklch(0.68 0.11 155);
  --chart-3: oklch(0.72 0.1 75);
  --chart-4: oklch(0.7 0.12 35);
  --chart-5: oklch(0.66 0.1 220);
}

@layer base {
  * {
    @apply border-border outline-ring/50;
  }
  body {
    @apply bg-background text-foreground;
    font-family: var(--font-space-grotesk);
    position: relative;
  }
}

@layer base {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  .font-display {
    font-family: var(--font-fraunces);
  }
}

@layer utilities {
  .animate-gradient-x {
    animation: gradient-x 8s ease infinite;
  }
}

@keyframes gradient-x {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

body::before {
  content: "";
  position: fixed;
  inset: 0;
  background:
    radial-gradient(600px 420px at 5% 0%, rgba(72, 175, 171, 0.12), transparent 60%),
    radial-gradient(520px 360px at 95% 5%, rgba(244, 190, 123, 0.12), transparent 60%),
    radial-gradient(520px 360px at 20% 90%, rgba(109, 190, 140, 0.12), transparent 60%);
  pointer-events: none;
  z-index: -1;
}

body::after {
  content: "";
  position: fixed;
  inset: 0;
  background-image: radial-gradient(rgba(0, 0, 0, 0.08) 1px, transparent 0);
  background-size: 48px 48px;
  opacity: 0.08;
  pointer-events: none;
  z-index: -1;
}
